import React from "react";
import data from "./data";
import Miles from "./Miles";

const Intern = (props) => {
  return (
    <div className="">
      <div className="col-md-12 w-100">
        <div className="row d-flex row justify-content-between py-5 px-3 w-100 px-md-0">
          <div className="col-md-4 d-flex justify-content-md-center  align-items-center mb-md-0 mb-4 ">
            <img
              src={data[props.index].photo}
              alt="Profile Pic"
              className="fluid "
              style={{ minWidth: "190px", height: "190px" }}
            />
          </div>
          <div className="col-md-8 row-4 d-flex flex-column justify-content-center">
            <h3>Name : {data[props.index].name}</h3>
            <h3>Organisation : {data[props.index].organisation}</h3>
            <h3> Project : {data[props.index].project}</h3>
          </div>
        </div>
      </div>
      {data[props.index].miles.map((val, idx) => (
        <Miles mile={val} number={idx} />
      ))}
      <div style={{ minHeight: "100vh" }}>
        <div
          className="col-md-12 w-100 px-md-5 px-5 py-3 "
          style={{ backgroundColor: "#FFE34F" }}
        >
          <h1>Conclusion</h1>
        </div>
        <div className="row m-md-5 m-3 border-bottom">
          <div class="container">
            <div class="row">
              <div class="col-sm">{data[props.index].conclusion}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Intern;
