import React, { useState, useRef } from "react";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import SunEditor from "suneditor-react";
import "suneditor/dist/css/suneditor.min.css";
import axios from "axios";
import topicData from "../reUsableComponent/TopicData";
import SnackbarComponent from "../reUsableComponent/SnackbarComponent";

const defaultFonts = [
  "Arial",
  "Comic Sans MS",
  "Courier New",
  "Impact",
  "Georgia",
  "Tahoma",
  "Trebuchet MS",
  "Verdana",
];
const sortedFontOptions = [
  "Logical",
  "Salesforce Sans",
  "Garamond",
  "Sans-Serif",
  "Serif",
  "Times New Roman",
  "Helvetica",
  ...defaultFonts,
].sort();
const UseCase = () => {
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [message, setMessage] = useState("");
  const [severity, setSeverity] = useState("");

  const [valueOfSunEditor, setValueOfSunEditor] = useState("");
  const handleImageUploadBefore = (files, info) => {
    // uploadHandler is a function
    console.log(files, info, "hi");
  };
  const [subTopicData, setSubTopicData] = useState([]);
  const topicSelectHandler = async (event) => {
    subTopic.current.value = "";
    setSubTopicData(topicData[topic.current.value].subtopic);
  };

  const title = useRef(null);
  const repoLink = useRef(null);
  const workflowLink = useRef(null);
  const historyLink = useRef(null);
  const notebookLink = useRef(null);
  const photo = useRef(null);

  const subTopic = useRef(null);
  const topic = useRef(null);
  const invForDiscussion = useRef(null);
  const submitHandler = async () => {
    try {
      const res = await axios.post("/api/v1/user/create/useCase", {
        title: title.current.value,
        topic: topicData[topic.current.value].topic,
        subTopic: subTopic.current.value,
        description: valueOfSunEditor,
        workflowLink: workflowLink.current.value,
        historyLink: historyLink.current.value,
        repoLink: repoLink.current.value,
        notebookLink: notebookLink.current.value,
        invForDiscussion: invForDiscussion.current.value,
      });

      const formData = new FormData();
      formData.append("image", photo.current.files[0]);
      const imageUploadRes = await axios.patch(
        `/api/v1/user/create/useCase/${res.data.useCase._id}`,
        formData
      );
      setOpenSnackbar(true);
      setMessage(res.data.message);
      setSeverity("success");
      console.log(res, imageUploadRes);
    } catch (error) {
      setOpenSnackbar(true);
      setMessage(error.response.data.message);
      setSeverity("error");
    }
  };

  return (
    <div className="m-3 pb-5 mt-5 rounded shadow bg-body">
      <SnackbarComponent
        message={message}
        severity={severity}
        open={openSnackbar}
        setOpen={setOpenSnackbar}
      />
      <div className="row">
        <div className="col-md-6 col-12">
          <div className="row">
            <Form>
              <Form.Group
                as={Row}
                className="my-3 ms-md-5 ms-1 "
                controlId="formPlaintextPassword"
              >
                <Form.Label column sm="1">
                  Tittle:
                </Form.Label>
                <Col sm="10">
                  <Form.Control
                    type="text"
                    className="shadow"
                    placeholder="Tittle"
                    ref={title}
                  />
                </Col>
              </Form.Group>
            </Form>
            <Form>
              <Form.Group
                as={Row}
                className="my-3 ms-md-5 ms-1 "
                controlId="formPlaintextPassword"
              >
                <Form.Label column sm="3">
                  Select Image:
                </Form.Label>
                <Col sm="8">
                  <Form.Control
                    type="file"
                    className="shadow"
                    accept="image/*"
                    ref={photo}
                  />
                </Col>
              </Form.Group>
            </Form>
          </div>
        </div>

        <div className="col-md-6 col-12">
          <div className="row">
            <div className=" col-12">
              <Form>
                <Form.Group
                  as={Row}
                  className="my-3 ms-md-5 ms-1"
                  controlId="formPlaintextPassword"
                >
                  <Form.Label column sm="3">
                    Select Topic :
                  </Form.Label>
                  <Col sm="8">
                    <Form.Select
                      className="shadow"
                      ref={topic}
                      onChange={topicSelectHandler}
                    >
                      <option>Select Topic</option>
                      {topicData.map((item, key) => (
                        <option value={key}>{item.topic}</option>
                      ))}
                    </Form.Select>
                  </Col>
                </Form.Group>
              </Form>
            </div>
            <div className=" col-12">
              <Form>
                <Form.Group
                  as={Row}
                  className="my-3 ms-md-5 ms-1"
                  controlId="formPlaintextPassword"
                >
                  <Form.Label column sm="3">
                    Select Sub_topic :
                  </Form.Label>
                  <Col sm="8">
                    <Form.Select className="shadow" ref={subTopic}>
                      <option>Select Sub Topic</option>

                      {subTopicData.map((item, key) => (
                        <option value={item}>{item}</option>
                      ))}
                    </Form.Select>
                  </Col>
                </Form.Group>
              </Form>
            </div>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-md-12 mx-5 ">Description :</div>
        <div className="col-12 d-flex justify-content-center my-5  ">
          <SunEditor
            width="90%"
            setContents={valueOfSunEditor}
            onChange={setValueOfSunEditor}
            onImageUpload={handleImageUploadBefore}
            setOptions={{
              buttonList: [
                [
                  "bold",
                  "underline",
                  "italic",
                  "strike",
                  "subscript",
                  "superscript",
                ],
                ["undo", "redo"],
                ["font", "fontSize"],
                ["fontColor", "hiliteColor"],
                ["align", "list", "lineHeight"],

                ["table", "horizontalRule", "link"],
              ],
              defaultTag: "div",
              minHeight: "300px",
              showPathLabel: false,
              font: sortedFontOptions,
            }}
          />
        </div>
      </div>
      <div className="row">
        <div className="col-md-6 col-12">
          <Form>
            <Form.Group
              className="my-3 ms-md-5 ms-1 "
              controlId="formPlaintextPassword"
            >
              <Form.Label column sm="12">
                Workflow link:
              </Form.Label>
              <Col sm="9">
                <Form.Control
                  type="url"
                  className="shadow"
                  placeholder="Workflow link"
                  ref={workflowLink}
                />
              </Col>
            </Form.Group>
          </Form>
        </div>
        <div className="col-md-6 col-12">
          <Form>
            <Form.Group
              className="my-3 ms-md-5 ms-1 "
              controlId="formPlaintextPassword"
            >
              <Form.Label column sm="12">
                History link:
              </Form.Label>
              <Col sm="9">
                <Form.Control
                  type="url"
                  className="shadow"
                  placeholder="History link"
                  ref={historyLink}
                />
              </Col>
            </Form.Group>
          </Form>
        </div>
      </div>
      <div className="row">
        <div className="col-md-6 col-12">
          <Form>
            <Form.Group
              className="my-3 ms-md-5 ms-1 "
              controlId="formPlaintextPassword"
            >
              <Form.Label column sm="12">
                Relevant Notebook link:
              </Form.Label>
              <Col sm="9">
                <Form.Control
                  type="url"
                  className="shadow"
                  placeholder="Relevant Notebook link"
                  ref={notebookLink}
                />
              </Col>
            </Form.Group>
          </Form>
        </div>
        <div className="col-md-6 col-12">
          <Form>
            <Form.Group
              className="my-3 ms-md-5 ms-1 "
              controlId="formPlaintextPassword"
            >
              <Form.Label column sm="12">
                Github repo/link:
              </Form.Label>
              <Col sm="9">
                <Form.Control
                  type="url"
                  className="shadow"
                  placeholder="Github repo/link"
                  ref={repoLink}
                />
              </Col>
            </Form.Group>
          </Form>
        </div>
      </div>
      <div className="row">
        <div className="col-md-9 col-12">
          <Form>
            <Form.Group
              as={Row}
              className="my-3 ms-md-5 ms-1 "
              controlId="formPlaintextPassword"
            >
              <Form.Label column sm="4">
                Invite For Discussion :
              </Form.Label>
              <Col sm="8">
                <Form.Control
                  type="text"
                  className="shadow"
                  placeholder="Invite For Discussion "
                  ref={invForDiscussion}
                />
              </Col>
            </Form.Group>
          </Form>
        </div>
        <div className="col-md-3 col-12">
          <div className="row">
            <div className=" col-12 my-3 ms-md-5 ms-1">
              <button
                onClick={submitHandler}
                style={{
                  background: "#2C3143",
                  borderRadius: "8px",
                  color: "#FFE34F",
                  border: "none",
                  padding: "10px  20px",
                }}
              >
                <strong>Submit</strong>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default UseCase;
