import React, { useState, useEffect } from "react";
import Pagination from "@mui/material/Pagination";
import axios from "axios";

import { useNavigate } from "react-router-dom";
const limit = 24;

const ExperimentalDesignAndStudiesTNB = () => {
  const [totalNoteBook, setTotalNoteBook] = useState(0);
  const [
    ExperimentalDesignAndStudiesData,
    setExperimentalDesignAndStudiesData,
  ] = useState([]);
  const getData = async (page) => {
    const res = await axios.get(
      `/api/v1/user/get/ExperimentalDesignAndStudies/toolNotebook/${limit}/${page}`
    );
    console.log(res);
    setTotalNoteBook(res.data.totalUserToolNoteBook);
    setExperimentalDesignAndStudiesData(res.data.toolnotebook);
  };
  const paginationData = (e, pageNo) => {
    console.log(pageNo);
    getData(pageNo);
  };

  useEffect(() => {
    getData(1);
  }, []);

  const navigate = useNavigate();
  return (
    <div className="m-5">
      <h3 className="my-5">ExperimentalDesignAndStudies</h3>
      <div>
        <div className=" row ">
          {ExperimentalDesignAndStudiesData.map(
            (ExperimentalDesignAndStudiesData) => (
              <div className="col-md-6 col-12">
                <p key={ExperimentalDesignAndStudiesData._id}>
                  <div
                    className="pointer"
                    onClick={(e) =>
                      navigate(ExperimentalDesignAndStudiesData._id)
                    }
                  >
                    {ExperimentalDesignAndStudiesData.title}
                  </div>
                </p>
              </div>
            )
          )}
        </div>
      </div>
      <div className="row  ">
        <div className="col d-flex justify-content-end">
          <Pagination
            count={Math.trunc(totalNoteBook / limit) + 1}
            onChange={paginationData}
          />
        </div>
      </div>
    </div>
  );
};

export default ExperimentalDesignAndStudiesTNB;
