import React, { useEffect } from "react";

import { NavLink, Outlet, useNavigate, useParams } from "react-router-dom";

const Contribute = () => {
  const navigate = useNavigate();
  const params = useParams();

  useEffect((e) => {
    navigate("/contribute/use_case");
  }, []);
  return (
    <div>
      <h1 className="d-flex justify-content-center py-2">
        Choose what to upload
      </h1>
      <div
        className="row  p-2"
        style={{ backgroundColor: " #2C3143", color: "white" }}
      >
        <NavLink
          className="col-12 col-md-4 d-flex justify-content-center rounded cursor-pointer dom-link"
          style={({ isActive }) => {
            return {
              backgroundColor: isActive ? "rgb(255, 250, 123)" : "",
              color: isActive ? "black" : "",
            };
          }}
          to={`/contribute/use_case`}
        >
          <h1>Use Case</h1>
        </NavLink>

        <NavLink
          className="col-12 col-md-4 d-flex justify-content-center rounded cursor-pointer dom-link"
          style={({ isActive }) => {
            return {
              backgroundColor: isActive ? "rgb(255, 250, 123)" : "",
              color: isActive ? "black" : "",
            };
          }}
          to={`/contribute/tool_notebook`}
        >
          <h1>Tool Notebook</h1>
        </NavLink>
        <NavLink
          className="col-12 col-md-4 d-flex justify-content-center rounded cursor-pointer dom-link"
          style={({ isActive }) => {
            return {
              backgroundColor: isActive ? "rgb(255, 250, 123)" : "",
              color: isActive ? "black" : "",
            };
          }}
          to={`/contribute/notebook`}
        >
          <h1>Notebook</h1>
        </NavLink>
      </div>
      <div>
        <Outlet />
      </div>
    </div>
  );
};

export default Contribute;
