import React from 'react'

console.log(1)
const Miles = (props) => {
    // console.log()
    return (
        <>
            <div className="col-md-12 w-100 px-md-5 px-5 py-3 " style={{ backgroundColor: "#FFE34F" }}>
                <h1>Mile {props.number + 1}</h1>
            </div>

            {props.mile.map((val) => (
                <div className="row m-md-5 m-3 mb-5">
                    <div class="container">
                        <div class="row">
                            <div class="col-sm">
                                {val.content}
                            </div>
                            <div class="col-sm">
                                <img src={val.pic} />
                            </div>
                        </div>
                    </div>
                </div>
            ))}
        </>
    )
}

export default Miles